import React, { useState } from 'react';
import { FaAlignRight, FaTimes } from 'react-icons/fa';

import exordeLogo from '../images/logo.svg';
import Sidebar from './sidebar';

const menu = [
  { id: 'home', label: 'Home', url: '' },
  { id: 'economics', label: 'Economics', url: '' },
];

const Navbar = () => {
  const [openSidebar, setOpenSidebar] = useState(false);
  return (
    <>
      <nav className="sticky pr-6 pl-6 pt-2 pb-2 h-20 w-full flex items-center z-50">
        <img src={exordeLogo} alt="exorde logo" className="float-left h-full" />
        <ul className="flex invisible md:visible ml-auto mr-auto">
          {menu.map(item => (
            <li className="p-2 hover:text-blue-dark hover:font-medium hover:border-b-2 hover:border-blue-dark">
              {item.label}
            </li>
          ))}
        </ul>
        <button
          type="button"
          onClick={() => setOpenSidebar(!openSidebar)}
          className="float-right text-3xl text-blue md:invisible"
        >
          {openSidebar ? <FaTimes /> : <FaAlignRight />}
        </button>
      </nav>
      <Sidebar open={openSidebar} />
    </>
  );
};

export default Navbar;
